<template>
  <div id="box">
    <div class="content">
        <div class="section">
            <div class="order-title">
                <span class="order-num">订单编号 <i>{{orderId}}</i></span>
                <span class="order-time">{{orderTime}}</span>
            </div>
            <div class="order-content">
                <img :src="productImage" alt="">
                <div class="text-right">
                    <div class="title-head">
                        <div>{{productName}}</div>
                        <span style="color:red;">￥{{amount}}</span>
                    </div>
                    <div class="title-info">
                        <div class="infor">{{skuValue}}</div>
                        <div class="numbers">x{{buyNum}}</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="section">
            <div class="user-info">
                <img :src="headImgUrl" alt="">
                <div>{{consignee}}</div>
            </div>
            <div class="item">
                <label>
                    收货地址
                </label>
                <span>
                    <p>{{address}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    联系人
                </label>
                <span>
                    <p>{{fansName}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    联系电话
                </label>
                <span>
                    <p>{{phone}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    支付金额
                </label>
                <span>
                    <p><i style="color:red;">￥{{amount}}</i></p>
                </span>
            </div>
            <div class="item">
                <label>
                    支付时间
                </label>
                <span>
                    <p class="flex-b">
                        <span>{{payDate}}</span>
                        <button :num="orderStatus" v-if="orderStatusTxt!=''">{{orderStatusTxt}}</button>
                    </p>
                </span>
            </div>
            <div class="item">
                <label>
                    快递单号
                </label>
                <span>
                    <p>{{trackingNo}}</p>
                </span>
            </div>
            <div class="item">
                <label>
                    备注
                </label>
                <span>
                    <p v-if="memo!=''">{{memo}}</p>
                    <p v-if="memo==''">暂无备注</p>
                </span>
            </div>
        </div>
    </div>
  </div>
</template>
<script>
    import com from '../assets/common.js';
    import Exotic from '../config.js';
    export default{
        created(){
            let that=this;
            if(this.$route.query.token!=undefined){
                this.token=this.$route.query.token;
            }
            if(this.$route.query.id!=undefined){
                this.id=this.$route.query.id;
            }
            that.MintUi.Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            })
            Exotic.ajaxData(that.token,Exotic.urlMap.groupbuy.order, {orderId: that.id}, 'get',function(data){
                console.log(data);
                //订单ID
                that.orderId=data.orderId;
                //订单时间
                var createDate = new Date(data.createDate).Format("yyyy-MM-dd hh:mm:ss");
                that.orderTime=createDate;
                //商品图片
                that.productImage=Exotic.imageUrl + data.productImage;
                that.productName=data.productName; //商品名字
                that.skuValue=data.skuValue; //规格
                that.buyNum=data.buyNum; //购买数

                //-------------------------------------------
                
                that.headImgUrl=data.headImgUrl; //用户头像
                that.consignee=data.consignee; //用户名称
                that.address=data.address; //地址
                that.fansName=data.fansName; 
                that.phone=data.phone; 
                that.amount=data.amount; 

                var payDate = new Date(data.payDate).Format("yyyy-MM-dd hh:mm:ss");
                that.payDate=payDate;
                if(data.trackingNo == null){
                    that.trackingNo=""
                }else {
                     that.trackingNo=data.trackingNo;
                }
                that.memo=data.memo;
                that.orderStatusTxt=data.orderStatusTxt;
                that.orderStatus=data.orderStatus; 
                that.MintUi.Indicator.close();
            });
        },
        data(){
            return {
                token:null,
                id:'',//ID
                orderId:null,//id
                orderTime:null,//时间
                productImage:'',//商品图片
                productName:'',//商品标题
                skuValue:null,
                buyNum:null,//
                headImgUrl:'',//用户头像
                consignee:'',
                address:'',//地址
                fansName:'',
                phone:'',
                amount:'',
                payDate:'',
                trackingNo:'',
                memo:'',
                orderStatusTxt:'',
                orderStatus:''
            }
        },
        methods:{
            
        },
        mounted(){
          
        }
    }
</script>
<style lang="scss">
@import '../assets/index.scss';
</style>




